import React, {useState} from 'react'
import {jsPDF} from "jspdf"
import 'jspdf-autotable'
import autoTable from 'jspdf-autotable';
//import autoTable from 'jspdf-autotable';
//import html2canvas from 'html2canvas'
//import { float } from 'html2canvas/dist/types/css/property-descriptors/float';

    let count = 0;
    let totalArray = [];
    let grossTotal = 0;

export default function TextForm() {
    const [text, setText] = useState('');
    const [number, setNumber] = useState('');
    const [textPN, setProductName] = useState('');

    //for invoice number
    const [num, setNum] = useState(0);

    const [inputarr, setInputarr] = useState([]);

    let discount = 0.00 ;
    // let count = 0;
    // let totalArray = [];
    // let grossTotal = 0;

    let imgData = 'data:image/png;base64,'
    
    
    const invoiceNumber = (min,max)=>{
       return Math.floor(Math.random() * (max - min + 1)) + min;
    }
    


    const generatePdf = async ()=>{
       

        if(text.length === 0 || number.length < 10 || !document.getElementById('calenderDate').value){
            alert("Please fill all the fields");
        }
        else{

        setNum(invoiceNumber(10000000000000,99999999999999));

        let newText = text.toUpperCase();
        setText(newText);

        let newNumber = number;
        setNumber(newNumber);

        let newProductName = textPN.toUpperCase();
        setProductName(newProductName);

        const doc = new jsPDF('p','pt','a4');
            
        const arr = doc.getFontList();
        console.log(arr);
        
        // to get width and height of the pdf page.
        const wi = doc.internal.pageSize.getWidth();
        console.log("width= " +wi);
        const hei = doc.internal.pageSize.getHeight();
        console.log("width= " +hei);

        const xOffSet1 = (doc.internal.pageSize.width/2) - (doc.getStringUnitWidth("ANN-NOOR CONTRUCTION")*10);
        const xOffSet2 = (doc.internal.pageSize.width/2) - (doc.getStringUnitWidth("IQBAL(BAVA) CHATI")*10);
        const xOffSet3 = (doc.internal.pageSize.width/2) - (doc.getStringUnitWidth("PHONE: 9898079952")*9);
        const xOffSet4 = (doc.internal.pageSize.width/2) - (doc.getStringUnitWidth("TA & DIST. BHARUCH, GUJARAT, 392240")*9);

        //set the width of all lines in pdf.
        doc.setLineWidth(2);

        //  doc.rect(5, 5, 585, 830)
    
        doc.setFont('Times', 'normal');
        doc.setFontSize(20);
        doc.text("ANN-NOOR CONTRUCTION", xOffSet1, 30);
        //590

        doc.line(115, 40, 590, 40);

        //stting image into the documents
        doc.addImage(imgData, 'PNG', 5, 5, 110, 110);

        doc.setFontSize(18);
        doc.text("IQBAL(BAVA) CHATI", xOffSet2, 63); 
        doc.setFontSize(16);
        doc.text("PHONE: 9898079952", xOffSet3, 83); 
        doc.text("TA & DIST. BHARUCH, GUJARAT, 392240", xOffSet4, 103);
        
        doc.line(5, 115, 590, 115);
        doc.setFontSize(14);
        doc.text("Invoice Number : ", 10, 135); 
        doc.text("       " + num, 118, 135);
        doc.text("Customer Name : ", 10, 155); 
        doc.text("       " + document.getElementById('customerName').value, 118, 155);
        doc.text("Customer Number : ", 10, 175); 
        doc.text("       " + document.getElementById('customerContactNumber').value, 118, 175);
        doc.text("Date : ", 10, 195); 
        doc.text("       " + document.getElementById('calenderDate').value, 118, 195);

        // doc.autoTable({
        //     html: "#productsInfoTable",
        //     startY: 190,
        //     theme: 'grid',
        // });
        autoTable(doc, {
             html : '#productsInfoTable',
             startY: 210,
             theme: 'grid',
             headStyles: {halign: 'center', fillColor: 'gray'},
             bodyStyles: {halign: 'right'},
             footStyles: {halign: 'right', fillColor: 'gray'},
             tableWidth: 585,
             margin: {left: 5},
        });

        doc.rect(5, 5, 585, 830)

        doc.line(5, 725, 590, 725);
        doc.text("Authorized Signatory", 450, 750);
        doc.text("Terms & conditions.", 15, 825)

        //below line is for name to save pdf.
        const customer_name = document.getElementById('customerName').value;
    
        doc.save(`${customer_name.toUpperCase()}.pdf`);
        }
    }


    const addNewProduct =()=>{

        if(!document.getElementById('productQuantity').value || document.getElementById('productQuantity').value === 0){
            alert("Please add a quantity");
        }
        else{
        
        const product_name = document.getElementById('productName').value.toUpperCase();
        const product_length = document.getElementById('productLength').value;
        const product_width = document.getElementById('productWidth').value;
        const product_height = document.getElementById('productHeight').value;
        const product_quantity = document.getElementById('productQuantity').value;
        const product_rate = document.getElementById('productRate').value;

        let total = product_quantity * product_rate;
        //shoud be in loop
        totalArray[count] = total;
        grossTotal = grossTotal + total;  
        console.log("totalarray= "+ totalArray);
        console.log("grosstotal= "+grossTotal);
        console.log("count= "+count);
        count++;
        

        //methon 2
        setInputarr([...inputarr,{count,product_name,product_length,product_width,product_height,product_quantity,product_rate,total}])

        console.log(inputarr);

        }
    }

    





    const handleOnChange = (event) =>{
        setText(event.target.value)
    }
    const handleOnChange1 = (event) =>{
        setNumber(event.target.value)
    }
    const handleOnChange2 = (event) =>{
        setProductName(event.target.value)
    }
    
  return (

    <>
    <div className='myInfo'>
        <div className='container my-1' >
            <h1 style={{backgroundColor: '#0d6efd', textAlign: 'center', color: 'white'}}>ANN-NOOR CONSTRUCTION</h1>
        </div>
    </div>
    <form>
        <div className="form-group container my-1">
            <label htmlFor="customerName">Customer Name :</label>
            <input type="text" className='form-control' id='customerName' onChange={handleOnChange} placeholder='Enter Name here' aria-describedby="customerNameHelp" required/>  
        </div>
        <div className="form-group container my-1">
            <label htmlFor="customerContactNumber">Contact Number :</label>
            <input type="number" className='form-control' id='customerContactNumber' onChange={handleOnChange1} placeholder='Enter Number here' required/>  
        </div>
        <div className="form-group container my-1">
            <label htmlFor="calenderDate">Date :</label>
            <input type="date" className='form-control' id='calenderDate' onChange={handleOnChange} required/>  
        </div>     
    </form>

    {/*     products information below    */}
    <form className='container my-3'>

                <div className="container my-3">
                    <button type='button' className="btn btn-primary float-end mx-1 my-1" onClick={addNewProduct} id='addButton'>Add</button>
                </div>
        <table align='left'>

            <tbody>
            <tr>
                <td>Product Name :</td>
                <td><input type="text" className='form-control mx-1 my-1' id='productName' style={{width: '100%',display: 'inline-block'}} onChange={handleOnChange2} placeholder='Enter Product Name here' required/></td>
            </tr>

            <tr>
                <td>Length :</td>
                <td><input type="text" className='form-control mx-1 my-1' id='productLength' style={{width: '100%',display: 'inline-block'}} defaultValue={0} onChange={handleOnChange} aria-describedby="customerNameHelp"/></td>
            </tr>

            <tr>
                <td>Width :</td>
                <td><input type="text" className='form-control mx-1 my-1' id='productWidth' style={{width: '100%',display: 'inline-block'}} defaultValue={0} onChange={handleOnChange} aria-describedby="customerNameHelp"/></td>
            </tr>

            <tr>
                <td>Height :</td>
                <td><input type="text" className='form-control mx-1 my-1' id='productHeight' style={{width: '100%',display: 'inline-block'}} defaultValue={0} onChange={handleOnChange} aria-describedby="customerNameHelp"/></td>
            </tr>

            <tr>
                <td>Quantity :</td>
                <td><input type="text" className='form-control mx-1 my-1' id='productQuantity' style={{width: '100%',display: 'inline-block'}} defaultValue={1} onChange={handleOnChange} aria-describedby="customerNameHelp"/></td>
            </tr>

            <tr>
                <td>Rate :</td>
                <td><input type="text" className='form-control mx-1 my-1' id='productRate' style={{width: '100%',display: 'inline-block'}} defaultValue={0} onChange={handleOnChange} aria-describedby="customerNameHelp"/></td>
            </tr>

            </tbody>

        </table><br />
    </form><br />

    <table style={{
                    width: '100%',
                    marginLeft: 'auto',
                    marginRight: 'auto'}} className='table table-bordered container' id='productsInfoTable'>
        <thead>
            <tr style={{textAlign: 'center'}}>
                <th style={{width: '6%'}}>Sr. No.</th>
                <th>Product Name</th>
                <th>Length</th>
                <th>Width</th>
                <th>Height</th>
                <th>Quantity</th>
                <th style={{width: '18%'}}>Rate</th>
                <th style={{width: '20%'}}>Total</th>
            </tr>
        </thead>
        <tbody style={{textAlign: 'right'}}> 
            {
                inputarr.map(
                    (info,ind)=>{
                            return(
                                <tr key={ind}>
                                    <td>{ind+1}</td>
                                    <td>{info.product_name}</td>
                                    <td>{info.product_length}</td>
                                    <td>{info.product_width}</td>
                                    <td>{info.product_height}</td>
                                    <td>{info.product_quantity}</td>
                                    <td>{info.product_rate}</td>
                                    <td>{info.total}</td>
                                </tr>
                            )
                    }
                )
            }
        </tbody>
        <tfoot>
            <tr>
                <th colSpan={6} style={{textAlign: 'right'}}>Total Discount : </th>
                <td colSpan={2} style={{textAlign: 'right'}}>{discount} Rs</td>
            </tr>
            <tr>
                <th colSpan={6} style={{textAlign: 'right'}}>Gross Total - Discount Included : </th>
                <td colSpan={2} style={{textAlign: 'right'}}>{grossTotal} Rs</td>
            </tr>
            <tr>
                <th colSpan={6} style={{textAlign: 'right'}}>Grand Total : </th>
                <td colSpan={2} style={{textAlign: 'right'}}>{grossTotal+discount} Rs</td>
            </tr>
            
        </tfoot>
    </table>
    
                <div className='button'>
                    <button type='button' className="btn btn-primary btn-block position-relative btn-lg mx-1 my-1" onClick={generatePdf} style={{left: '50%',width: 'auto'}}>Save</button>
                </div>
    </>
  )
  
}
